.login-body {
  width: 100%;
  height: 100vh;
  margin: 0;
  background-color: #fafbff;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "Inter";
  font-style: normal;
}
.login-body form {
  width: 35vw;
  min-width: 350px;
  overflow: auto;
  padding: 40px;
  background: #ffffff;
  box-shadow: 0px 4px 45px rgba(0, 0, 0, 0.06);
  border-radius: 25px;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  flex-direction: column;
}
.login-body form .headings {
  margin-bottom: 40px;
}
.login-body form .headings h1 {
  font-weight: 700;
  font-size: 28px;
  line-height: 34px;
  margin: 0px;
}
.login-body form .headings p {
  margin-top: 10px;
  font-weight: 400;
  font-size: 15px;
  line-height: 18px;
  display: flex;
  align-items: center;
  color: #666666;
}
.login-body form label {
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  display: flex;
  align-items: center;
  color: #282828;
}
.login-body form .txt-inp {
  display: block;
  box-sizing: border-box;
  width: 70%;
  height: 50px;
  border: 1px solid #e0e0e0;
  border-radius: 7px;
  padding: 10px;
  margin: 10px 0px 30px 0px;
}
.login-body form button {
  width: 70%;
  height: 45px;
  background: #133b5c;
  color: #ffffff;
  border-radius: 7px;
}
.login-body form button:hover {
  background: #ffffff;
  border: 2px solid black;
  color: #133b5c;
  transition: all 0.75s ease-in-out;
}
.login-body form a {
  margin: 20px 0px;
  color: #0b2a43;
}
.login-body form span {
  margin-bottom: 20px;
}
.login-body .sideDesign {
  width: 20vw;
  min-width: 250px;
  height: 550px;
  background: linear-gradient(181.36deg, #133b5c -27.88%, #032038 142.94%);
  box-shadow: 0px 4px 45px rgba(0, 0, 0, 0.11);
  border-radius: 20px;
  position: relative;
  display: flex;
  align-items: center;
}
.login-body .sideDesign p {
  width: 100%;
  position: absolute;
  bottom: 0;
  font-weight: 400;
  font-size: 12px;
  color: #108efa;
  display: flex;
  justify-content: center;
}
.login-body .sideDesign div {
  height: 50%;
  width: 100%;
  position: relative;
}
.login-body .sideDesign div .img {
  width: 100%;
  height: 100%;
  opacity: 0;
  animation: vis 9s ease-out 7s infinite;
}
.login-body .sideDesign div .svg1 {
  width: 30%;
  height: 40px;
  position: absolute;
  bottom: 139px;
  left: 35%;
}
.login-body .sideDesign div .svg1 svg {
  animation-name: moveT;
  animation-duration: 9s;
  animation-timing-function: ease-out;
  animation-iteration-count: infinite;
  animation-delay: 7s;
}
.login-body .sideDesign div .svg1 svg path {
  animation: colorChangeT 9s ease-out 7s infinite;
}
.login-body .sideDesign div .svg2 {
  width: 30%;
  height: 40px;
  position: absolute;
  top: 134px;
  left: 35%;
  background-color: rgba(12, 47, 75, 0.6);
  filter: blur(3px);
  -webkit-backdrop-filter: blur(6px);
          backdrop-filter: blur(6px);
  animation: unBlur 9s ease-out 7s infinite;
}
.login-body .sideDesign div .svg2 svg {
  animation-name: moveB;
  animation-duration: 9s;
  animation-timing-function: ease-out;
  animation-iteration-count: infinite;
  animation-delay: 7s;
}
.login-body .sideDesign div .svg2 svg path {
  animation: colorChangeB 9s ease-out 7s infinite;
}

@keyframes moveT {
  0% {
    transform: translateX(0px);
    width: 100%;
  }
  3.5% {
    transform: translateX(0px);
    width: 100%;
  }
  13.5% {
    transform: translateX(-70px);
    width: 50%;
  }
  28.5% {
    transform: translateX(0px);
    width: 100%;
  }
  63.5% {
    transform: translateX(0px);
    width: 100%;
  }
  73.5% {
    transform: translateX(95px);
    width: 50%;
  }
  88.5% {
    transform: translateX(0px);
    width: 100%;
  }
  100% {
    transform: translateX(0px);
    width: 100%;
  }
}
